/* harmony default export */ __webpack_exports__["a"] = ({id:"1009",title:"Klussen in Fowey",created:"7 juni 2018",location:"Fowey",geolocation:{lat:50.335878,lng:-4.634598},paragraphs:["We liggen een week in Fowey, Cornwall. We gebruiken deze week om wat laatste klussen aan de boot te doen en Bess grondig schoon te maken. We vervangen alle lijnen die oud en groen uitgeslagen zijn, doen nog wat kitwerk en we zetten de witte opbouw in de wax. We doen de was bij Tom's Boat Yard. En natuurlijk maken we ook een paar wandelingen rond de rivier.","We liggen op een mooie plek en er is van alles te zien op de rivier: zeil- en motorboten die binnenkomen en vertrekken, watertaxi's, pontjes voor voetgangers, rondvaartbootjes, kinderen die leren zeilen. Maar ook grote vrachtboten die de rivier op varen naar de grote aardewerkfabriek van Fowey. Vlak voor onze neus draaien die schepen met behulp van een sleepboot om achteruit de steeds smallere rivier te worden opgesleept."],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1009_7_juni_2018_lex_stort_zich_op_het_poetsen_van_het_schuifluik_800px_jpg___default.a,title:'Lex stort zich op het poetsen van het schuifluik.'},{src:__WEBPACK_IMPORTED_MODULE_1__photos_1009_7_juni_2018_dankzij_jarenlang_geknutsel_aan_zijn_brommer_weet_lex_alles_van_bougies_800px_jpg___default.a,title:'Dankzij jarenlang geknutsel aan zijn brommer weet Lex alles van bougies.'},{src:__WEBPACK_IMPORTED_MODULE_2__photos_1009_7_juni_2018_tijdens_het_wandelen_zien_we_bess_liggen_800px_jpg___default.a,title:'Tijdens het wandelen zien we Bess liggen.'},{src:__WEBPACK_IMPORTED_MODULE_3__photos_1009_7_juni_2018_kinderen_krijgen_zeilles_800px_jpg___default.a,title:'Kinderen krijgen zeilles.'},{src:__WEBPACK_IMPORTED_MODULE_4__photos_1009_7_juni_2018_kate_c_komt_even_buurten_800px_jpg___default.a,title:'Kate C komt even buurten.'}]});

