/* harmony default export */ __webpack_exports__["a"] = ({id:"1012",title:"Een week A CoruÃ±a",created:"22 juni 2018",location:"A CoruÃ±a",geolocation:{lat:43.38500000,lng:-8.60361111},paragraphs:["Rond de kaap van Finisterre staat veel wind, dus we blijven een weekje in de beschutte haven van A Coru\xF1a liggen. We  maken schoon schip en wisselen dat af met uitjes, klussen en lezen.","De stad heeft prachtige gebouwen met gallerijen, maar ook een historische kern en aan de noordkant een mooi strand. Op het plein naast de haven is van alles te doen: drie dagen een voedselfestival, muziekoptredens en verkiezingsbijeenkomsten. In een caf\xE9 kijken we tussen de autochtonen naar de voetbalwedstrijd Spanje-Iran.","We nemen de bus naar Sada en maken daar een wandeling langs de kust door het bos. En we bezoeken Santiago de Compostello. De kathedraal van Santiago is vooral bekend als eindpunt voor alle wandelaars die de Cami\xF1o hebben gelopen. Dat is de Sint Jacobsweg, een belangrijke katholieke pelgrimsroute die al sinds de 9e eeuw bestaat.","Iedere dag komen er honderden het plein op lopen. Jong en oud. Ze lopen vanuit de relatieve stilte van het wandelpad de hectiek van de drukke binnenstad in. Dat geeft bij velen een euforisch gevoel. We spreken Engelse meisjes die de laatste 105 km in zes dagen gelopen hebben. Maar ook een Oostenrijkse mevrouw die de hele route van de Franse grens (804 km) in 34 dagen gelopen heeft."],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1012_22_juni_2018_ben_en_lex_maken_schoon_schip_800px_jpg___default.a,title:"Ben en Lex maken schoon schip."},{src:__WEBPACK_IMPORTED_MODULE_1__photos_1012_22_juni_2018_kustwandeling_800px_jpg___default.a,title:"Prachtige wandeling langs steile kust van Sada"},{src:__WEBPACK_IMPORTED_MODULE_2__photos_1012_22_juni_2018_karakteristieke_gevel_coruna_800px_jpg___default.a,title:"Karakteristieke gevel in A Coru\xF1a"},{src:__WEBPACK_IMPORTED_MODULE_3__photos_1012_22_juni_2018_noordkust_800px_jpg___default.a,title:"Bij de noordkust van A Coru\xF1a gaat de zee fors tekeer."},{src:__WEBPACK_IMPORTED_MODULE_4__photos_1012_22_juni_2018_gevels_coruna_800px_jpg___default.a,title:"De aanblik van A Coru\xF1a bestaat uit een grote gallerij van gevels."},{src:__WEBPACK_IMPORTED_MODULE_5__photos_1012_22_juni_2018_santiago_800px_jpg___default.a,title:"Lange rijen om de kathedraal van Santiago de Compastello in te gaan."}]});

