/* harmony default export */ __webpack_exports__["a"] = ({id:"1015",title:"Effe Jelle bellen",created:"12 juli 2018",location:"Villanova",geolocation:{lat:42.81638889,lng:-9.09333333},paragraphs:["In april 2017 vervangen we onze 20 jaar oude navigatieapparatuur. In onze jachthaven in Hindeloopen plaatst de refit, i.c. elektriciteitsman Jelle, een volledig nieuw systeem aan boord. Er komt een mooie plotter boven onze kaarttafel met vier nieuwe klokjes (drie boven het schuifluik en een bij het stuurwiel) waar we alle relevante gegevens in allerlei varianten op af kunnen lezen: waypoints, koers, wind, diepte, snelheid en gegevens van andere schepen om ons heen via de AIS. Na wat aanvangsproblemen (de vier frisse klokjes zijn binnen een week van boord gestolen) doet het spul het helemaal goed en worden wij over de Noordzee en de Golf van Biskaje feilloos naar nieuwe bestemmingen geleid.","Maar tussen Muros en Riveira gaat het mis.","Opeens hebben we geen GPS-fix (contact met satellieten) meer, de dieptemeters slaan op hol, waypoints zijn volledig de weg kwijt. We zijn weer aangewezen op ons kompas en eyeball navigation om de volgende haven binnen te lopen. Wat nu?","We besluiten Jelle de elektriciteitsman te bellen. Hij legt ons geduldig en stap voor stap uit wat wij in hier in Riveira zelf kunnen doen om dit probleem op te lossen. Een systeemupdate van Raymarine om te beginnen. Daarna vooral je gezonde verstand gebruiken, en de FAQ's op de site van Raymarine raadplegen.","Na twee dagen en diep nadenken hebben we de boel weer aan de praat. Een enorme opluchting, want we zijn inmiddels zo gewend aan het gemak van digitale informatie. De zeiltocht naar Villanova (\xB1 10 mijl) verloopt de dag daarna ouderwets en in alle rust."],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1015_12_juli_2018_de_gps_doet_het_weer_800px_jpg___default.a,title:"De klokjes boven het schuifluik werken weer als een zonnetje."}]});

