/* harmony default export */ __webpack_exports__["a"] = ({id:"1016",title:"Grote en kleine vissers (1)",created:"14 juli 2018",location:"VilagarcÃ­a de Arousa",geolocation:{lat:42.60111111,lng:-8.77250000},paragraphs:["De vangst van vissen en schelpen is een belangrijke bron van inkomsten in de Ria's van Spanje. De Ria's liggen vol met \"viveiros\" (fishfarms). Kleine open bootjes zijn aan het werk met laag water. De grote visboten varen op het diepe deel van de Ria's en op zee.","De grote vissers gebruiken sleepnetten om vis te vangen. De bodem van de Ria's is rotsachtig, blijkbaar hebben de sleepnetten daarvan te lijden. In Ribeira - achter de vissershaven - is een groot terrein waar die netten gecontroleerd, hersteld en \"opgenet\" worden. De netten worden met vrachtwagens gebracht. Een stuk op 10 man zijn daar elke dag aan het werk, vaak in de zon, zonder schaduw en met alleen hun handen als gereedschap. Zwaar werk."],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1016_14_juli_2018_de_netten_worden_met_een_karretje_neergelegd_800px_jpg___default.a,title:"De netten worden met een karretje neergelegd."},{src:__WEBPACK_IMPORTED_MODULE_1__photos_1016_14_juli_2018_controleren_repareren_is_handwerk_800px_jpg___default.a,title:"Controleren, repareren en \"opnetten\" is handwerk."}]});

