/* harmony default export */ __webpack_exports__["a"] = ({id:"1017",title:"De wondere wereld van wifi",created:"15 juli 2018",location:"VilagarcÃ­a de Arousa",geolocation:{lat:42.60111111,lng:-8.77250000},paragraphs:["In IJmuiden gaat het voor het eerst mis. Bij het inchecken bij Seaport Marina krijgen we te horen dat vanwege \"de recente storm\" de wifipaal op halfzeven hangt. Geen wifisignaal in de haven dus. We moeten uitwijken naar het dichtstbijzijnde strandpaviljoen om een berichtje te kunnen verzenden en weersvoorspellingen binnen te halen. Geen probleem, kan iedereen gebeuren. En het bijbehorende glaasje witte wijn met uitzicht op zee smaakt er niet minder om.","Eenmaal aan de Spaanse overkant wordt het een standaardverhaal bij het inchecken. \"Ja, we hebben wifi, maar juist vandaag is er een storing opgetreden.\" Of: \"We hebben de beste verbinding van heel Galici\xEB, maar nu even niet vanwege een technisch mankement. Probeer het even bij de bar van de buren.\" En: \"Over een paar dagen hebben we weer verbinding, echt waar.\" ","Voor hetzelfde geld scheur je wat later met een snelheid van 200mb/sec over het net. Doet de paal het opeens weer subliem. En hebben we via wifi een superieur TV-sportweekend aan boord met de finales in Wimbledon, twee etappes van de Tour en ook nog de finale van het WK voetbal."]});

