/* harmony default export */ __webpack_exports__["a"] = ({id:"1019",title:"Ons",created:"24 juli 2018",location:"Portonovo",geolocation:{lat:42.40305556,lng:-8.82472222},paragraphs:["Ons en Cies zijn twee eilandjes. Ze liggen voor de ingang van de Ria Pontevedra (Ons) en de Ria de Vigo (Cies). Ze beschermen deze twee zuidelijke ria's tegen het geweld van de oceaan. Niet voor niets dat de Spaanse overheid de eilanden tot nationaal park verheven heeft. Dat betekent dat je een 'permit' moet hebben om er te mogen varen, ankeren en duiken. We hebben een permit aangevraagd voor Ons en voor Cies.","Als we bij Ons aankomen, zit de wind in de verkeerde hoek. De zee bouwt op en de ankerplek is zeer hobbelig. Daar hebben we geen zin in. We varen door naar Portonovo - de eerstvolgende haven  - en nemen het pontje.","Op de boot veel Spanjaarden die naar Ons gaan voor de mooie stranden. Op Ons zijn verschillende wandelingen uitgezet. We nemen de hele dag om het eiland rond te lopen en eindigen met een duik in zee op een van de mooie stranden.","Op Ons zijn twee bijzonderheden. Volgens de informatieborden leven er grote reptielen die op het vasteland niet meer voorkomen. We hebben ze niet kunnen zien.","Aan de westkant van het eiland beuken de golven van de oceaan constant op de kust. In de loop der eeuwen zijn er 'furnaces' ontstaan. Dit zijn ondergrondse grotten. Bij sterke golfslag zorgt het in- en uitstromend water voor een loeiend geluid. Zo heftig dat men dacht dat bij de 'Buraco do inferno' de doorgang was naaar de onderwereld en je de dolende zielen kon horen."],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1019_24_juli_2018_de_oostkant_van_ons_is_heuvelachtig_800px_jpg___default.a,title:"De oostkant van Ons is heuvelachtig."},{src:__WEBPACK_IMPORTED_MODULE_1__photos_1019_24_juli_2018_de_oceaan_maakt_de_grillige_westkust_van_ons_800px_jpg___default.a,title:"De oceaan maakt de grillige westkust van Ons."},{src:__WEBPACK_IMPORTED_MODULE_2__photos_1019_24_juli_2018_de_ondergrondse_grot_bij_buraco_do_inferno_800px_jpg___default.a,title:"De ondergrondse grot bij Buraco do inferno."}]});

