/* harmony default export */ __webpack_exports__["a"] = ({id:"1020",title:"Wat zou jij doen?",created:"27 juli 2018",location:"Islas Cies",geolocation:{lat:42.21666667,lng:-8.90000000},paragraphs:["Een van de mooiste ankerplekjes van heel Galici\xEB is bij het eiland Cies (spreek uit: Sie-j\xE8s). We besluiten er twee nachten te ankeren.","De eerste nacht is altijd nog even wennen. Gelukkig heeft onze plotter ankeralarm, d.w.z. dat het apparaat begint te piepen zodra je te ver verwijderd raakt van je anker. We slapen licht, maar we liggen als een huis.","De volgende dag willen we met de dinghy naar wal om op het eiland te wandelen. Helaas begint tijdens het ontbijt de wind flink door te staan naar 5 \xE0 6 bft, inclusief irritante golven, en dan laten wij ons bootje Bess niet alleen. Dat duurt tot vroeg in de avond, daarna zakt de wind in en kalmeert de zee.","'s Nachts zetten wij weer ons ankeralarm. Dat gaat rond 5 uur 's ochtends opeens af. We blijken 180 graden gedraaid te zijn. Het is nog pikkedonker buiten, maar de zee is rustig. Wat nu te doen? Stellen we het ankeralarm opnieuw in vanaf de nieuwe positie? Gaan we er maar van uit dat alles goed gaat met ons anker? Of moeten we een andere oplossing bedenken?","Wat zou jij doen? Je kunt ons een mailtje sturen met jouw suggestie(s). We komen er binnenkort op terug."],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1020_27_juli_2018_ankerplek_cies_800px_jpg___default.a},{src:__WEBPACK_IMPORTED_MODULE_1__photos_1020_27_juli_2018_ankerplek_cies_2_800px_jpg___default.a}]});

