/* harmony default export */ __webpack_exports__["a"] = ({id:"1021",title:"Wat zou jij doen? (2)",created:"10 augustus 2018",location:"Punta Lagoa",geolocation:{lat:42.26527778,lng:-8.70833333},paragraphs:["Zo, daar zijn we weer. Op onze vraag over het ankeralarm hebben we ruim tien mailtjes binnengekregen. De vraag was wat jij zou doen als midden in de nacht het ankeralarm af zou gaan, en bleek dat het schip 180 graden was gedraaid. Er waren heel originele oplossingen bij:","- ik zou het schip weer rechtop zetten;","- ik zou het anker lichten, terugvaren naar Nederland, Bess verkopen en met een klein bootje Friesland in gaan;","- mijn vrouw wakker maken en mezelf weer omdraaien in bed.","Ook heel serieuze:","- ankeralarm opnieuw instellen op 54 meter, zodat je een behoorlijke marge hebt voor tij en swing;","- op het dek blijven, lekker in m'n slaapzak en de wal goed in de gaten blijven houden;","- motor aanzetten en kijken of het anker houdt;","- anker opnieuw uitgooien. Of twee ankers zetten.","Omdat het de vorige dag hard gewaaid had, en het schip behoorlijk had liggen draaien, zijn wij ervan uitgegaan dat het anker voldoende ingegraven was. We lagen immers nog steeds op dezelfde afstand van de schepen om ons heen (die dus ook allemaal 180 graden gedraaid waren) en even ver van de wal. De zee was inmiddels rustig en de wind gekalmeerd.","We keken elkaar aan en dachten allebei hetzelfde: het ankeralarm gaat niet meer aan, we vertrouwen op ons superieure Rocna-anker en en duiken ons bed weer in. Rust in de tent. Drie uur later werden we fris als een hoentje wakker. Uitstekend geslapen."]});

