/* harmony default export */ __webpack_exports__["a"] = ({id:"1027",title:"Noord-Portugal",created:"20 september 2018",location:"Cascais",geolocation:{lat:38.69972222,lng:-9.42444444},paragraphs:["We hebben de afgelopen weken een verdrietige tijd doorgemaakt. Maria (schoondochter) en Eelke (zoon) waren in verwachting van een tweeling. Helaas zijn de kinderen halverwege de zwangerschap overleden. Maria en Eelke wonen in het Zweedse Malm\xF6 en we zijn vanuit Porto op en neer gevlogen. Door deze gebeurtenis is dit blog er enige tijd bij ingeschoten. We pakken de draad nu weer op.","We hebben de Spaanse ria's verlaten en zijn via de noord-Portugese steden Viana de Castello en Povoa de Varzim in Cascais aangekomen (op de hoogte van Lissabon). Vergeleken met acht jaar geleden merken we dat de economie opbloeit. We zien veel renovatie en nieuwbouw, de straten zijn schoon, de mensen zien er verzorgd uit en er wordt veel minder gerookt op straat. We genieten van de Portugese ongedwongen sfeer.","Omdat het in noord-Portugal steeds meer herfst wordt met veel mist, besluiten we na ons bezoek aan Zweden in \xE9\xE9n keer door te varen naar Cascais. Een tocht van 2 \xE0 2,5 dag.","De mist dreigt nog roet in het eten te gooien, maar we gaan en gokken er op dat het op zee helder is. Mis gegokt. De eerste dag en nacht motoren we door een windstille wattendeken. De tweede dag kunnen we echt zeilen. Wat een genot. Maar bij het vallen van de avond komen we opnieuw in de mist. Gelukkig nu wel met wind. Als we midden in de nacht de laatste kaap voor Cascais ronden, trekt de mist op en kunnen we lichtjes op het land zien. Dankzij de GPS, de AIS, de plotter en onze eigen eye-ball navigation, leggen we om half 5 's ochtends in het pikkedonker aan bij de wachtsteiger van de haven. ","Klus geklaard, 180 mijl in de pocket. Het is hier weer zomer.","PS Kijk in het archief om twee nog niet gepubliceerde berichten van eind augustus te lezen."],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1027_20_september_2018_prachtige_oude_gebouwen_in_viana_de_castello_800px_jpg___default.a,title:"Prachtige oude gebouwen in Viana de Castello."},{src:__WEBPACK_IMPORTED_MODULE_1__photos_1027_20_september_2018_vanuit_povoa_de_varzim_bezoeken_we_porto_800px_jpg___default.a,title:"Vanuit Povoa de Varzim bezoeken we Porto."},{src:__WEBPACK_IMPORTED_MODULE_2__photos_1027_20_september_2018_we_eten_met_hans_en_christien_in_een_strandtentje_met_uitzicht_op_zee_mist_800px_jpg___default.a,title:"We eten met Hans en Christien in een strandtentje met uitzicht op zee(mist)."},{src:__WEBPACK_IMPORTED_MODULE_3__photos_1027_20_september_2018_de_strandtentjes_in_povoa_de_varzim_worden_opgeruimd_800px_jpg___default.a,title:"De strandtentjes in Povoa de Varzim worden opgeruimd."},{src:__WEBPACK_IMPORTED_MODULE_4__photos_1027_20_september_2018_wij_wachten_tot_de_mist_is_opgetrokken_800px_jpg___default.a,title:"Wij wachten tot de mist is opgetrokken."}]});

