/* harmony default export */ __webpack_exports__["a"] = ({id:"1028",title:"Cabo Espichel",created:"22 september 2018",location:"Cascais",geolocation:{lat:38.69972222,lng:-9.42444444},paragraphs:["De kaap ten zuiden van Lissabon is de Cabo Espichel. Meestal zien we die kapen vanaf het water. Maar deze zien we vanaf het land","Een oud-collega heeft op de Cabo Espichel een stuk grond. Om haar te bezoeken nemen we de trein naar Lissabon, de ferry over de Taag naar de zuidelijke rivieroever, de bus naar Santana. Daar worden we met de auto opgehaald en is het nog een kwartier rijden naar de Cantinho Alberto Caeiro. En dan zit je ook echt in de middle-of-nowhere","De collega en haar Portugese vriend wonen daar in een houten huisje op palen, met uitzicht op de oceaan. Ze hebben honden, poezen en een gans.","Op het land staan drie caravans en een boomhuisje dat ze verhuren. Alle voorzieningen zijn eco-vriendelijk: gebruik van zonne-energie, toiletten waarbij plas en poep met behulp van stro gebruikt kunnen worden voor compost, iedere caravan/boomhuisje heeft een buiten-douche met water dat door de zon verwarmd wordt. Beetje primitief, maar dat wordt ruimschoots goedgemaakt door de kleurrijke inrichting, de enorme plekken, het uitzicht op de oceaan en de totale rust. Het is vijf minuten lopen naar het strand. Vlak bij de kust is het land begroeid met ijsbloemen. Die zijn nu uitgebloeid, maar in mei moet het een wonder van schoonheid zijn","Voor foto's, zie hun website: http://cantinhocaeiro.wixsite.com/cantinhocaeiro/tree-caravans"],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1028_22_september_2018_woonhuis_van_de_cantinho_alberto_caeiro_800px_jpg___default.a,title:"Woonhuis van de Cantinho Alberto Caeiro."},{src:__WEBPACK_IMPORTED_MODULE_1__photos_1028_22_september_2018_uitzicht_op_de_oceaan_800px_jpg___default.a,title:"Uitzicht op de oceaan."}]});

