/* harmony default export */ __webpack_exports__["a"] = ({id:"1030",title:"Mouros Castelo en PalÃ¡cio da Pena",created:"24 september 2018",location:"Cascais",geolocation:{lat:38.69972222,lng:-9.42444444},paragraphs:["Ten noorden van Cascais ligt een bergrug. Daar is het koeler dan in Lissabon. In de middeleeuwen bouwen krijgsheren er kastelen. Vorsten en kooplieden hebben in de afgelopen eeuwen daar hun zomerpaleizen gebouwd. Het is teveel om in een dag te bekijken en we besluiten ons tot twee bouwwerken te beperken. We pakken de bus naar Sintra en stappen daar over op de bus naar het Mouros Castelo (moors kasteel) en het Pal\xE1cio da Pena.","We zijn niet de enigen. In Sintra staat bij de halte een rij van 75 meter; mensen die allemaal dezelfde bus willen nemen als wij. Dit is massatoerisme waar we niet helemaal op voorbereid zijn, de Portugezen duidelijk wel. Als de bus vol is en wegrijdt, komt de volgende alweer voorrijden.","De bus brengt ons omhoog via nauwe steile weggetjes. We stappen uit bij het Mouros Castelo, nu een archelogische site. Het is nog vroeg en niet druk, dus kunnen we op ons gemak de ru\xEFnes beklimmen. Dit waren in de middeleeuwen de eerste verdedigingslinies van Lissabon. Maar er zijn ook sporen gevonden van islamitische bewoning v\xF3\xF3r die tijd (10e eeuw).","Daarna lopen we door naar het Pal\xE1cio da Pena. Daar is het een drukte van belang. We staan drie kwartier in de rij om het paleis binnen te komen. Eenmaal binnen dringt het tot ons door dat dit paleis het paleis is waar de laatste koningen van Portugal in de zomer verbleven, tot 1910 toen de Oktoberrevolutie een einde maakte aan de Portugese monarchie. Stapje voor stapje worden we door het paleis geleid. We zien prachtige architectuur, mooie betegelde en beschilderde muren en een overdaad aan zilver en stijlvolle meubelen. Dit is het rijke verleden van Portugal van een eeuw geleden. Daarbij valt paleis Het Loo in het niet."],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1030_24_september_2018_de_verdedigingstorens_van_mouros_castelo_1_800px_jpg___default.a},{src:__WEBPACK_IMPORTED_MODULE_1__photos_1030_24_september_2018_de_verdedigingstorens_van_mouros_castelo_2_800px_jpg___default.a},{src:__WEBPACK_IMPORTED_MODULE_2__photos_1030_24_september_2018_de_verdedigingstorens_van_mouros_castelo_3_800px_jpg___default.a,title:"De verdedigingstorens van Mouros Castelo."},{src:__WEBPACK_IMPORTED_MODULE_3__photos_1030_24_september_2018_palacio_da_pena_zomerresidentie_van_de_laatste_koningen_van_portugal_1_800px_jpg___default.a},{src:__WEBPACK_IMPORTED_MODULE_4__photos_1030_24_september_2018_palacio_da_pena_zomerresidentie_van_de_laatste_koningen_van_portugal_2_800px_jpg___default.a},{src:__WEBPACK_IMPORTED_MODULE_5__photos_1030_24_september_2018_palacio_da_pena_zomerresidentie_van_de_laatste_koningen_van_portugal_3_800px_jpg___default.a,title:"PalÃ¡cio da Pena; zomerresidentie van de laatste koningen van Portugal."},{src:__WEBPACK_IMPORTED_MODULE_6__photos_1030_24_september_2018_prachtig_betegelde_binnenplaats_800px_jpg___default.a,title:"Prachtig betegelde binnenplaats."},{src:__WEBPACK_IMPORTED_MODULE_7__photos_1030_24_september_2018_rijk_gedecoreerd_van_binnen_800px_jpg___default.a,title:"Rijk gedecoreerd van binnen."}]});

