/* harmony default export */ __webpack_exports__["a"] = ({id:"1032",title:"Hoe kan dat nou? (2)",created:"30 september 2018",location:"Sines",geolocation:{lat:37.95111111,lng:-8.86972222},paragraphs:["In ons blog van 18 augustus verwonderden wij ons over het verschijnsel 'wind'. Op zee is verandering van de barometer een belangrijke indicator voor toenemende wind: als de barometer binnen een uur 1 punt zakt of stijgt, heb je grote kans op harde wind. Hoewel de barometer al dagen geen verandering in luchtdruk aangeeft, steekt er toch plotseling een straffe noordoosten wind op met vlagen tot 6 Beaufort. Hoe kan dat nou?","Dit was onze vraag. We hebben drie reacties gehad, vari\xEBrend van ik weet het niet, invloed van het zeewater tot invloed van bergen. Wij weten het ook niet precies, maar in grote lijnen (we zijn geen metereologen) denken we het volgende te snappen.","In Nederland trekken depressies van zuidwest naar noordoost. Zo'n beetje via Ierland, Engeland en Schotland over de Noordzee naar Scandinavi\xEB. Die route van de depressies zorgt ervoor dat de barometer daalt en stijgt. De wind draait om de depressie heen, tegen de klok in. In grote lijnen: ligt de depressie voor Ierland, dan hebben we in Nederland zuid en zuidwesten wind, is de depressie bij Schotland aangekomen, dan is de wind inmiddels west. Is de depressie bij Scandinavie aangekomen, dan zit de wind in Nederland in de noordwest hoek.","In Portugal warmt het land enorm op in de zomer. Daardoor stijgt de lucht en ontstaat er een lagedrukgebied boven het vastenland. De wind draait tegen de klok in. En dan komt in de Spaande ria's de wind uit de noordoost hoek en voor de Portugese kust zit de wind in het noorden: de Portugese Noord. Hoe later op de dag, hoe warmer het land en hoe harder het gaat waaien. Het lagedrukgebied verplaatst zich niet, en de barometer blijft hetzelfde.","De wind kan op twee manieren nog extra versterkt worden:","- als boven de Azoren het hogedrukgebied ligt. Bij een hogedrukgebied draait de wind met de klok mee. Voor de kust van Portugal is die dan noord. Dit versterkt de Portugese Noord die hier meestal waait;","- als er bergen in de buurt zijn. Dan ontstaan er valwinden waardoor de wind aantrekt. Dit was volgens ons aan de hand in de ria in Noord-Spanje.","Als we op zee een kaap ronden, krijg je hetzelfde effect. We houden rekening met extra wind en blijven dan ook meestal ver van de kaap vandaan.","Dit is wat wij er van snappen. Konden jullie het volgen?"]});

