/* harmony default export */ __webpack_exports__["a"] = ({id:"1033",title:"Antonio en Tiago. We laten ons graag verrassen!",created:"1 oktober 2018",location:"Sines",geolocation:{lat:37.95111111,lng:-8.86972222},paragraphs:["We vinden het leuk om concerten/voorstellingen te bezoeken in de plaatsjes waar we aanleggen en laten ons graag verrassen. In Sines treedt Antonio op; een jonge man met gitaar. Het publiek is gem\xEAleerd: gezinnen, mannen en vrouwen, oud en jong.","Antonio komt op, pakt zijn gitaar, zingt de eerst regel van een liedje, maakt een zwaaibeweging naar de zaal en vervolgens zingt iedereen uit volle borst mee. En dat niet \xE9\xE9n keer, maar twee uur lang. We denken dat alle populaire liedjes van Portugal langsgekomen zijn. Opgewekt en vol energie gaat we rond middernacht terug naar de boot.","Vier weken daarvoor gaan we in Povoa de Varzim naar het concert van Tiago Nacarato (ook een jonge man met gitaar). Het publiek bestaat vooral uit jonge vrouwen, een paar mannen en \xE9\xE9n grijze heer (Lex). We worden verrast door prachtige liedjes in een gecombineerde Portugees-Braziliaanse stijl, ondersteund door een hele goede band. Naarmate de avond vordert, wordt het publiek steeds enthousiaster en wordt er geklapt en gefloten. Tiago krijgt een staande ovatie en geeft wel vijf toegiften. We zijn lichtelijk overdonderd door deze reactie van het publiek. Terug aan boord zoeken we op internet.","Ah, nu begrijpen we het enthousiasme van het (vooral vrouwelijke) publiek.","Kijk en luister https://www.youtube.com/watch?v=xDE1LotmAqY"],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1033_01_oktober_2018_antonio_cassapo_800px_jpg___default.a},{src:__WEBPACK_IMPORTED_MODULE_1__photos_1033_01_oktober_2018_tiago_nacarato_800px_jpg___default.a}]});

