/* harmony default export */ __webpack_exports__["a"] = ({id:"1034",title:"Berusting en geduld",created:"14 oktober 2018",location:"Lagos",geolocation:{lat:37.10916667,lng:-8.67611111},paragraphs:["Onze laatste tocht van Sines `[Sienzj]` naar Lagos `[Lag\xF2ezj]` is zo'n 80 mijl. Met een gemiddelde snelheid van 5 mijl per uur doen we daar dus 16 uur over. We varen met een gereefd grootzeil (gescheurd langs de giek). Omdat we niet in het donker een haven binnen willen lopen, besluiten we 's middags uit Sines te vertrekken zodat we 's ochtends vroeg bij daglicht onze volgende bestemming zullen bereiken.","Wat schetst onze verbazing? Na vijf maanden met een ielig noorderwindje voor de wind gezeild te hebben, staat er nu een lekkere bries (ruime 4 bft) vanuit het westen, zodat we met een bloedgang (Bess heeft maar weinig wind nodig) naar het zuiden zeilen. Om 11 uur 's avonds ronden we de Cabo St. Vincente met 9 knopen snelheid.","Na de Cabo is het nog 20 mijl naar Lagos. Dus nu we komen toch nog veel te vroeg aan; het tempo moet er uit. We rollen de genua in en zetten nog twee riffen. Daarna is het gedaan met de pret. Vanaf Sagres zeilen we de resterende 20 mijl met een snelheidje van 1 \xE0 2 knopen per uur onder een schitterende hemel vol sterren en een halve maan. Omdat onze windvaan vastgelopen is, zitten we zelf uren achter het stuurwiel. Een goede oefening in berusting en geduld. Om half 8 's ochtends komt de zon stralend op. Om 09:00 uur melden wij ons in de zon bij het havenkantoor.","Wat een prachtige zeiltocht ter afsluiting van het zeilseizoen 2018.","P.S. Afgelopen zaterdagnacht heeft de orkaan Leslie de Portugese kust bereikt. Vooral rond Lissabon is de schade groot. Wij liggen ongeveer 250 kilometer zuidelijker, in de luwte van de zuidkust. Afgezien van wat lijntjes die we strakker moesten zetten vanwege onrustige windstoten, hebben we er weinig van gemerkt. Alleen heeft Bess b\xE0kken regen over zich heen gekregen."],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1034_14_oktober_een_schitterende_zonsopgang_vlak_voor_lagos_800px_jpg___default.a,title:'Een schitterende zonsopgang vlak voor Lagos'}]});

