/* harmony default export */ __webpack_exports__["a"] = ({id:"1038",title:"Mentaal inslingeren",created:"4 juni 2019",location:"Lagos",geolocation:{lat:37.10916667,lng:-8.67611111},paragraphs:["Als je een tijdje niet meer gevaren hebt, heeft je lichaam even tijd nodig om weer te wennen aan alle bewegingen van het schip. Continu gewiebel vanwege het golvende water doet een groot beroep op je evenwichtsorgaan. 'Inslingeren' heet dat in zeilersjargon. Na een half jaar weer terug in Lagos, hebben wij de afgelopen 2,5 week nog niet meer dan 100 meter gevaren (van de winterstalling naar de box in de marina). Maar toch moeten wij ook inslingeren, mentaal dan.","We helpen Bess uit haar winterkleren (dektent), slaan de zeilen aan, spuiten het woestijnzand van het schip, demonteren, smeren en monteren alle (zeven) lieren. En dan hebben we het nog niet eens over het interieur: we zetten de bankkussens op hun plek, sluiten de accu's aan, maken de bedden op, vullen de watertanks en de brandalcoholtanks (kooktoestel). En dan blijven er nog tientallen klussen over die ook gedaan moeten worden.","Belangrijkste vraag: waar gaan we de komende vier maanden naartoe? Het is natuurlijk een luxeprobleem, maar je kunt hier alle kanten op. We kunnen bijvoorbeeld eerst uitgebreid de zuidkust van Portugal en Spanje bekijken en daarna voor een maandje naar de zuidoost kust van Spanje (Costa del Sol) varen om daar zoveel mogelijk via het openbaar vervoer van het binnenland te bekijken. We kunnen ook een trip maken naar Marokko (Tanger, Rabat).","We zijn er nog niet uit. We hebben nog wat meer tijd nodig om in te slingeren. Het schip is er klaar voor en nu wij nog."],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1038_01_800px_jpg___default.a,title:"Ook in het laagseizoen ligt de marina van Lagos best vol"},{src:__WEBPACK_IMPORTED_MODULE_1__photos_1038_02_800px_jpg___default.a,title:"Veel poetswerk om al het zand van boord te krijgen"},{src:__WEBPACK_IMPORTED_MODULE_2__photos_1038_03_800px_jpg___default.a,title:"Secuur werkje, zo'n lier schoonmaken"},{src:__WEBPACK_IMPORTED_MODULE_3__photos_1038_04_800px_jpg___default.a,title:"Veel ooievaarsnesten, vol met jonge gezinnetjes"}]});

