/* harmony default export */ __webpack_exports__["a"] = ({id:"1039",title:"Achteraf lach je erom",created:"9 juni 2019",location:"PortimÄo",geolocation:{lat:37.13694444,lng:-8.52555556},paragraphs:["We zijn bijna vier weken aan boord van Bess en hebben de tijd genomen om de boot vaarklaar te maken en op te tuigen. Daarbij merken we dat we regelmatig vergeten zijn hoe we zaken ook al weer aanpakken. Vaak gaat het om knullige zaken. En als we het weer weten, schrijven we de aanpak als tip op in ons dagboek.","Tip 1. \"Sluit de startaccu aan als je voor het eerst de motor weer wil starten.\" Was een beetje rare gewaarwording, toen we nog onder de hijskraan in het water lagen en de motor doodstil bleef. De man van de haven vroeg nog vriendelijk: \"Did you remember to connect the battery?\" Ships.","Tip 2 is van de zelfde orde. \"Gebruik voor het slot van de buitenboordmotor het goede sleuteltje (die aan de kurken bol).\" Dit schoot ons weer te binnen, nadat we een half uur vruchteloos hadden geprobeerd ons veiligheidsslot met het sleuteltje van de kajuitingang open te maken.","De voorlopig laatste tip herinnerden we ons toen we met onze tong op onze tenen aan het nahijgen waren van het opblazen van de bijboot. \"Als de handpomp zwaar loopt, maak de pomp dan open en spuit wat WD40-olie op het rubber aan de schuif.\"","Ach ja....  's avonds een glaasje wijn in de kuip en we lachen smakelijk om onszelf."],photos:[]});

