/* harmony default export */ __webpack_exports__["a"] = ({id:"1040",title:"Een Engelse enclave",created:"14 juni 2019",location:"Albufeira",geolocation:{lat:37.08333333,lng:-8.26944444},paragraphs:["We hebben zes dagen voor anker gelegen in de prachtige baai van Portim\xE3o. Bijzonder aan deze plek is dat de wind steevast om 15:00 uur stevig begint door te staan (5-6 bft NW) en er pas rond 21:00 de brui aan geeft. Boodschappen, wandelen en sightseeing doen we daarom in de ochtend. 's Middags zitten we hotsenklotsend in de kuip en 's avonds zijn we doodmoe van de wind, alle herrie, het gewiebel en gefluit. Maar het anker geeft geen krimp, Bess ligt als een huis.","En nu liggen we zo'n 14 mijl verderop, in de haven van Albufeira. Een leuke, kleine haven met hele schappelijke tarieven. We gaan nu van het wintertarief naar het middentarief; dat betekent dat je per nacht ruim \u20AC 20 meer betaalt (van 13 naar 34 euro). Vanaf volgende maand tot eind augustus komt daar nog eens schepje bovenop. Niks bijzonders, hier in het zuiden.","Het gaat goed hier met de toeristenindustrie. Dat blijkt vooral als we in het binnenstadje een kijkje nemen. Dat ligt zo'n drie kilometer verderop. Albufeira is een toeristenoord vol souvenirshops, eetgelegenheden en bars. Een ware Engelse enclave ook, inclusief massa's blote bierbuiken, schommelende billen en diepe decollet\xE9s. De hoeveelheden eten en gigantische pullen bier zijn hier werkelijk niet aan te slepen. In de haven merk je daar gelukkig nauwelijks iets van."],photos:[{src:__WEBPACK_IMPORTED_MODULE_0__photos_1040_01_800px_jpg___default.a,title:"Schitterend strijklicht 's avonds in de ankerbaai."},{src:__WEBPACK_IMPORTED_MODULE_1__photos_1040_02_800px_jpg___default.a,title:"Het blauwe schip in het midden is Bess. Ook hier zijn we een klein bootje."},{src:__WEBPACK_IMPORTED_MODULE_2__photos_1040_03_800px_jpg___default.a,title:"Je leest wat af als je een paar dagen voor anker ligt."},{src:__WEBPACK_IMPORTED_MODULE_3__photos_1040_04_800px_jpg___default.a,title:"Tripperbootjes met toeristen varen af en aan langs de rotsen."},{src:__WEBPACK_IMPORTED_MODULE_4__photos_1040_05_800px_jpg___default.a,title:"Slim idee: lege blikjes krijgen een tweede leven als asbak op het strand. Peuken na afloop in de vuilnisbak, blikje leeg weer inleveren. Strand blijft schoon."},{src:__WEBPACK_IMPORTED_MODULE_5__photos_1040_06_800px_jpg___default.a,title:"Kleurrijke appartementen in de haven van Albufeira."}]});

